.pagination {
  margin-top: 60px;
  text-align: center;
  font-size: 16px;
  color: #3A3A3A; }

.arrow {
  border: solid #1C4277;
  border-width: 0 5px 5px 0;
  display: inline-block;
  padding: 7px;
  top: 6px;
  position: relative;
  border-radius: 2px;
  cursor: pointer;
  opacity: 0.5; }
  .arrow:hover {
    opacity: 1; }
  .arrow:active {
    opacity: 0.8; }

.right {
  left: 10px;
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg); }

.left {
  right: 10px;
  -ms-transform: rotate(135deg);
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg); }



