.main-block {
  background-color: #fff;
  -webkit-box-shadow: 0 3px 5px 1px #CCCCCC;
  box-shadow: 0 3px 5px 1px #CCCCCC;
  border-radius: 5px;
  padding: 25px;
  font-size: 14px;
  margin: 30px 0;
  overflow: hidden;
  position: relative; }
  .main-block--closed {
    height: 10px; }
  .main-block__title {
    font-weight: 600;
    color: #3A3A3A;
    margin: 0;
    font-size: 14px;
    padding: 0px 5px 5px 0;
    margin-bottom: 20px; }
  .main-block__show-btn {
    position: absolute;
    right: 20px;
    border: solid #3A3A3A;
    border-width: 0 4px 4px 0;
    display: inline-block;
    padding: 4px;
    cursor: pointer;
    opacity: 0.4; }
    .main-block__show-btn--down {
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      top: 25px; }
    .main-block__show-btn--up {
      -webkit-transform: rotate(-135deg);
      -ms-transform: rotate(-135deg);
      transform: rotate(-135deg);
      top: 28px; }
    .main-block__show-btn:hover {
      opacity: 1; }

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .main-block {
    max-width: 940px;
    margin: 30px auto; } }



